.header {
  width: 100%;
  height: 80px;
  line-height: 80px;
  text-align: center;
  margin: 0;
}
:global .am-list-content {
  flex-grow: 0;
  flex-shrink: 0;
}
:global .am-list-extra {
  flex-grow: 1;
  flex-shrink: 1;
}
:global .show_money_type .am-list-body {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
:global .am-list-item .am-input-control input {
  font-size: 14px;
}
:global .am-textarea-control textarea {
  font-size: 14px;
}
